import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { BrowserTab } from '@ionic-native/browser-tab';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { DreamJobPage } from '../pages/dream-job/dream-job';
import { SkillsPage } from '../pages/skills/skills';
import { JobSearchPage } from '../pages/job-search/job-search';
import { CitySearchPage } from '../pages/city-search/city-search';
import { GigResultPage } from '../pages/gig-result/gig-result';
import { RelatedJobsPage } from '../pages/related-jobs/related-jobs';

import { JobDataProvider } from '../providers/job-data/job-data';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    DreamJobPage,
    SkillsPage,
    JobSearchPage,
    CitySearchPage,
    GigResultPage,
    RelatedJobsPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp,{
      mode: 'ios'
    }),
    HttpClientModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    DreamJobPage,
    SkillsPage,
    JobSearchPage,
    CitySearchPage,
    GigResultPage,
    RelatedJobsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    BrowserTab,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    JobDataProvider
  ]
})
export class AppModule {}



