import { Component, OnInit } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';

import { GigResultPage } from '../../pages/gig-result/gig-result';
import { RelatedJobsPage } from '../../pages/related-jobs/related-jobs';
import { JobDataProvider } from '../../providers/job-data/job-data';
import { jobInformation, skillInformation } from '../../assets/data/dataModel';


@Component({
  selector: 'page-skills',
  templateUrl: 'skills.html',
})

export class SkillsPage {

	currentJob: jobInformation = null;
	dreamJob: jobInformation = null;
	location: string = null;

	currentJobUuid: string;
	dreamJobUuid: string;

	skillsOwned: any = [];
	skillsNeeded: skillInformation[] = [];
	
	skillsSelected: skillInformation[] = [];
	skillsSelectedLength: number = 0;
	skillsNeededLength: number;

	constructor(public navCtrl: NavController,
				private _loadingCtrl: LoadingController,
				private _alertCtrl: AlertController,
				public navParams: NavParams,
				private _jobDataProvider: JobDataProvider) {



		this.currentJob = navParams.get('currentJob');
		this.dreamJob = navParams.get('dreamJob');
		this.location = navParams.get('location');

	}

	ngOnInit(){

		this._jobDataProvider.getUuid(this.currentJob.id)
			.subscribe(res => {
				let job_uuid = res.uuid;
				console.log(job_uuid);

				this._jobDataProvider.getSkillset(job_uuid)
					.subscribe(res => {
						console.log(res);

						for (let skill of res.skills){
							if (skill.importance > 3)
								this.skillsOwned.push(skill.skill_name);
						}

						console.log(this.skillsOwned)

						this._jobDataProvider.getUuid(this.dreamJob.id)
							.subscribe(res => {

								let job_uuid = res.uuid;

								this._jobDataProvider.getSkillset(job_uuid)
									.subscribe(res => {
										console.log("----")
										console.log(res);

										for (let skill of res.skills){
											if ((skill.importance > 3) && (this.skillsOwned.indexOf(skill.skill_name) < 0))
												this.skillsNeeded.push({
													skill_name: skill.skill_name,
													description: skill.description,
													importance: skill.importance,
													selected: false,
													uuid: skill.skill_uuid						
												})
										}

										console.log(this.skillsNeeded);

										this.skillsNeededLength = this.skillsNeeded.length;

										// this.presentIntruction();

									})

							})
					})

			})

		let loading = this._loadingCtrl.create({
			spinner: 'dots'
		})

		loading.present();

		setTimeout(() =>{
			loading.dismiss();
		}, 1000);

	}

	presentJobDescriptionAlert(skill: skillInformation){
		let jobDescriptionAlert = this._alertCtrl.create({
			title: skill.skill_name,
			message: skill.description,
			buttons: ['Dismiss']
		});

		jobDescriptionAlert.present();
	}

	presentIntruction(){
		let instructionAlert = this._alertCtrl.create({
			title: "test",
			message: "In order to become " + this.dreamJob.occupation + "from " + this.currentJob.occupation + 
					 ", we have identified " + this.skillsNeededLength + " skills that you need to work on. \r\n" +
					 "The skills are ranked based on the importance. Click on the skill to read more about the skill. \r\n" +
					 "You can select up to 3 skills that you hope to gain.",
			buttons: ['Dismiss']
		});

		instructionAlert.present();
	}

	presentMaxSkillAlert(){
		let maxSkillAlert = this._alertCtrl.create({
			title: "",
			message: "Sorry, you can only select up to 3 skills",
			buttons: ['Dismiss']
		});

		maxSkillAlert.present();
	}

	popView(){
		this.navCtrl.pop();
	}

	pushSkill(index:number, event: Event){
		event.stopPropagation();

		if (this.skillsSelectedLength < 3  || this.skillsNeeded[index].selected){
			this.skillsNeeded[index].selected = !this.skillsNeeded[index].selected;
			if (this.skillsNeeded[index].selected)
				this.skillsSelectedLength ++;
			else 
				this.skillsSelectedLength --;			
		} else {
			this.presentMaxSkillAlert();
		}


	}

	pushPage(){
		console.log(this.skillsSelectedLength);
		this.skillsSelected = [];

		for (let skill of this.skillsNeeded) {
			if (skill.selected)
				this.skillsSelected.push(skill);
		}
		console.log(this.skillsSelected);

		// this.navCtrl.push(GigResultPage, {
		// 	currentJob: this.currentJob,
		// 	dreamJob: this.dreamJob,
		// 	location: this.location,
		// 	skillsOwned: this.skillsOwned,
		// 	skillsSelected: this.skillsSelected
		// })

		this.navCtrl.push(RelatedJobsPage, {
			currentJob: this.currentJob,
			dreamJob: this.dreamJob,
			location: this.location,
			skillsOwned: this.skillsOwned,
			skillsSelected: this.skillsSelected			
		})
	}

	delete(chip: Element) {
	    chip.remove();
	}
}



