import { Component, OnInit } from '@angular/core';
import { NavController, NavParams, ModalController } from 'ionic-angular';

import { JobDataProvider } from '../../providers/job-data/job-data';
import { jobInformation} from '../../assets/data/dataModel';
import { DreamJobPage } from '../dream-job/dream-job';
import { SkillsPage } from '../skills/skills';
import { JobSearchPage } from '../job-search/job-search';
import { CitySearchPage } from '../city-search/city-search';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})

export class HomePage {

	jobList: jobInformation[] = [];
	cityList: string[] = [];
	currentJob: jobInformation = {
		id: '',
		occupation: '',
		group: ''
	}

	dreamJob: jobInformation = {
		id: '',
		occupation: '',
		group: ''		
	}
	location: string = '';

	constructor(public navCtrl: NavController,
				public modalCtrl: ModalController, 
				private _jobDataProvider: JobDataProvider) {

	}

	ngOnInit(){

		this._jobDataProvider.getJobData()
			.subscribe(res =>{
				for (let job of res){
					let jobInstance: jobInformation = {
						id: job.id,
						occupation: job.occupation,
						group: job.group
					}
					this.jobList.push(jobInstance);
				}
			})
		this._jobDataProvider.getCityData()
			.subscribe(res => {
				for (let city of res){
					let cityInstance = city.city + ', ' + city.state;
					this.cityList.push(cityInstance)
				}
			})
	}

	pushPage(){

		this.navCtrl.push(SkillsPage,{
			currentJob: this.currentJob,
			dreamJob: this.dreamJob,
			location: this.location
		});
	}

	openJobSearchModal(index: number){

		if (index ==1) {
			let searchModal = this.modalCtrl.create(
				JobSearchPage,{
					jobList: this.jobList,
					title: "Select your most recent job"
				}
			)

			searchModal.present()
			searchModal.onDidDismiss((selectedItem) =>{
				console.log(selectedItem);
				if (selectedItem != null) 
					if (index == 1)
						this.currentJob = selectedItem
					else if (index == 2)
						this.dreamJob = selectedItem
			})
		} else{
			let searchModal = this.modalCtrl.create(
				JobSearchPage,{
					jobList: this.jobList,
					title: "Select your dream job"
				}
			)	

			searchModal.present()
			searchModal.onDidDismiss((selectedItem) =>{
				console.log(selectedItem);
				if (selectedItem != null) 
					if (index == 1)
						this.currentJob = selectedItem
					else if (index == 2)
						this.dreamJob = selectedItem
			})		
		}	


	}

	openCitySearchModal(){
		let searchModal = this.modalCtrl.create(
			CitySearchPage,{
				cityList: this.cityList,
				title: "Please select your city"
			}
		)	

		searchModal.present()
		searchModal.onDidDismiss((selectedItem) =>{
			console.log(selectedItem);
			this.location = selectedItem
		})		
	}

}



