import { Component, OnInit } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';

import { GigResultPage } from '../../pages/gig-result/gig-result';
import { JobDataProvider } from '../../providers/job-data/job-data';
import { jobInformation, skillInformation, relatedJobInformation } from '../../assets/data/dataModel';

@Component({
  selector: 'page-related-jobs',
  templateUrl: 'related-jobs.html',
})

export class RelatedJobsPage {


	currentJob: jobInformation = null;
	dreamJob: jobInformation = null;
	location: string = null;
	skillsOwned: any = [];
	skillsSelected: skillInformation[] = [];
	relatedJobs: relatedJobInformation[] = [];
	relatedJobsLength: number = 0;

	constructor(public navCtrl: NavController,
				private _loadingCtrl: LoadingController,
				private _alertCtrl: AlertController,
				public navParams: NavParams,
				private _jobDataProvider: JobDataProvider) {

		this.currentJob = navParams.get('currentJob');
		this.dreamJob = navParams.get('dreamJob');
		this.location = navParams.get('location');
		this.skillsOwned = navParams.get('skillsOwned');
		this.skillsSelected = navParams.get('skillsSelected')

	}

	ngOnInit(){
		console.log(this.skillsSelected);

		this._jobDataProvider.getRelatedJobs(this.skillsSelected)
			.subscribe(res => {
				this.relatedJobs = res;
				console.log(this.relatedJobs);
				loading.dismiss();
				this.relatedJobsLength = this.relatedJobs.length;
			})

		let loading = this._loadingCtrl.create({
			spinner: 'dots'
		})

		loading.present();

		// setTimeout(() =>{
		// 	loading.dismiss();
		// }, 5000);
	}

	presentExploreAlert(index: number){
		let exploreAlert = this._alertCtrl.create({
			title: "Explore Gigs",
			message: "Explore gigs related to " + this.relatedJobs[index].job_title,
			buttons: [{
				text: 'Confirm',
				handler: () => {
					this.pushPage(this.relatedJobs[index]);
				}
			}]
		});

		exploreAlert.present();
	}

	popView(){
		this.navCtrl.pop();
	}

	pushPage(jobSelected: relatedJobInformation){
		this.navCtrl.push(GigResultPage, {
			currentJob: this.currentJob,
			dreamJob: this.dreamJob,
			location: this.location,
			skillsOwned: this.skillsOwned,
			skillsSelected: this.skillsSelected,
			jobSelected: jobSelected
		})
	}

}



