import { Component, OnInit } from '@angular/core';
import { ViewController, NavParams } from 'ionic-angular';
import { jobInformation} from '../../assets/data/dataModel';


@Component({
  selector: 'page-job-search',
  templateUrl: 'job-search.html',
})
export class JobSearchPage {

  title: string = '';
  searchQuery: string = '';
  items: string[] = [];
  jobList: jobInformation[] = [];
  occupationList: string[] = [];

  constructor(public viewCtrl: ViewController, 
  			  public navParams: NavParams) {
  	this.initializeItems();
  	this.jobList = navParams.get('jobList');
    this.title = navParams.get('title')
  }

  initializeItems() {
  	this.items = this.occupationList;
  }


  ngOnInit(){
  	for (let job of this.jobList){
    	this.occupationList.push(job.occupation)
    }
  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();

    // set val to the value of the searchbar
    let val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.items = this.items.filter((item) => {
        return (item.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
  }

  selectJob(occupation: any) {
  	for (let job of this.jobList){
  		if (job.occupation.toLowerCase() == occupation.toLowerCase()) {
  			console.log(job)
  			this.viewCtrl.dismiss(job)
  		}
  	}
  }

  closeModal(){
  	this.viewCtrl.dismiss()
  }

}



