import { Component, OnInit } from '@angular/core';
import { NavController, NavParams, ModalController } from 'ionic-angular';

import { JobDataProvider } from '../../providers/job-data/job-data';
import { jobInformation} from '../../assets/data/dataModel';
import { SkillsPage } from '../../pages/skills/skills';
import { JobSearchPage } from '../../pages/job-search/job-search';

@Component({
  selector: 'page-dream-job',
  templateUrl: 'dream-job.html',
})
export class DreamJobPage {

	jobList: jobInformation[] = [];
	currentJob: jobInformation = null;
	dreamJob: jobInformation = {
		id: '',
		occupation: '',
		group: ''
	}

	constructor(public navCtrl: NavController, 
				public navParams: NavParams,
				public modalCtrl: ModalController,
				private _jobDataProvider: JobDataProvider) {
		this.currentJob = navParams.get('currentJob');
		console.log(this.currentJob);
	}

	ngOnInit(){
		
		this.dreamJob = {
			id: '13-2011.01',
			occupation: 'Accountants',
			group: 'Business and Financial Operations'
		}

		this._jobDataProvider.getJobData()
			.subscribe(res =>{
				for (let job of res){
					let jobInstance: jobInformation = {
						id: job.id,
						occupation: job.occupation,
						group: job.group
					}
					this.jobList.push(jobInstance);
				}
			})

	}

	popView(){
		this.navCtrl.pop();
	}

	pushPage(){
		this.navCtrl.push(SkillsPage, {
			currentJob: this.currentJob,
			dreamJob: this.dreamJob
		})
	}

	openSearchModal(){
		let searchModal = this.modalCtrl.create(
			JobSearchPage,{
				jobList: this.jobList
			}
		)

		searchModal.present()
		searchModal.onDidDismiss((selectedJob) =>{
			console.log(selectedJob);
			if (selectedJob != null) this.dreamJob = selectedJob
		})
	}
}



