import { Component, OnInit } from '@angular/core';
import { NavController, LoadingController, NavParams } from 'ionic-angular';
import { BrowserTab } from '@ionic-native/browser-tab';

import { JobDataProvider } from '../../providers/job-data/job-data';
import { gigInformation, jobInformation, skillInformation, relatedJobInformation } from '../../assets/data/dataModel';

@Component({
  selector: 'page-gig-result',
  templateUrl: 'gig-result.html',
})
export class GigResultPage {

	currentJob: jobInformation = null;
	dreamJob: jobInformation = null;
	location: string = "";

	skillsOwned: string[] = [];
	skillsSelected: string[] = [];
	jobSelected: relatedJobInformation = null;

	gigList: gigInformation[] = [];
	gigListLength: number = null;
	volunteerGigList: gigInformation[] = [];
	volunteerGigListLength: number = null;

	segment:number = null;
	selectedTab: string = "";

	constructor(public navCtrl: NavController, 
			    private _loadingCtrl: LoadingController,
				public navParams: NavParams,
				private _jobDataProvider: JobDataProvider,
				private _browserTab: BrowserTab) {
		this.selectedTab = "job"

		this.currentJob = navParams.get('currentJob');
		this.dreamJob = navParams.get('dreamJob');
		this.location = navParams.get('location');
		this.skillsOwned = navParams.get('skillsOwned');
		this.jobSelected = navParams.get('jobSelected');

		for (let skill of navParams.get('skillsSelected')) {
			this.skillsSelected.push(skill.skill_name);
		}


		console.log(this.skillsSelected);
		console.log(this.jobSelected)
	}

	ngOnInit(){

		let loading = this._loadingCtrl.create({
			spinner: 'dots'
		})

		loading.present();

		this._jobDataProvider.getGigs(this.currentJob, 
									  this.dreamJob, 
									  this.location, 
									  this.skillsOwned, 
									  this.skillsSelected,
									  this.jobSelected)
			.subscribe(res => {
				console.log(res)

				this.gigList = res.jobs.gigs;
				this.gigListLength = this.gigList.length;

				this.volunteerGigList = res.volunteers.gigs;
				this.volunteerGigListLength = this.volunteerGigList.length;	

				this.segment = 1;

				loading.dismiss();
			})
	}

	changeSegment(index:number){
		this.segment = index;
	}

	openGig(){
		this._browserTab.isAvailable()
			.then((isAvailable: boolean) => {

				if (isAvailable){
					this._browserTab.openUrl("https://google.com");
				} else {	
				}
				
			})
	}


	popView(){
		this.navCtrl.pop();
	}

}



